* {
	margin: 0;
	padding: 0;
}

.bg2 {
	position: absolute;
	z-index: 0;
	background-color: transparent;
	left: 0px;
	top: 90px;
}
.bg3 {
	position: absolute;
	z-index: 0;
	background-color: transparent;
	transform: scaleX(-1);
	right: 0px;
	top: 750px;
}
.head {
	margin-top: 2%;
	text-align: center;
}
.head2 {
	margin-top: 7%;
	text-align: center;
}
hr {
	width: 30%;
	margin-left: 35%;
}
.hr2 {
	padding-bottom: 3%;
	width: 30%;
	margin-left: 35%;
	margin-top: -12px;
}
.main {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: auto;
	grid-gap: 2%;
	grid-row-gap: 5%;
	margin: auto 5%;
	margin-left: 4%;
}
.main .card {
	background-color: transparent;
	width: 100%;
	border-radius: 5px;
	border: 1px solid rgba(126, 126, 126, 0.459);
}
.main .card h1 {
	color: black;
	font-size: 25px;
	text-align: center;
	padding-top: 5%;
	padding-bottom: 1%;
}
.main .card .image img {
	width: 100%;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}
.main .card .des {
	text-align: center;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
}
.main .card .b2 {
	margin-top: 10px;
	margin-bottom: 20px;
	background-color: transparent;
	border: .5px solid black;
	border-radius: 4px;
	padding: 10px;
	font-size: 17px;
	background-color: rgba(243, 243, 243, 0.5);
	cursor: pointer;
}
@media (max-width: 670px) {
	.bg2 {
		position: fixed;
		z-index: 0;
		background-color: transparent;
		left: 0px;
		top: 200px;
		opacity: .8;
	}
	.bg3 {
		display: none;
