* {
	margin: 0;
	padding: 0;
}
.body {
	overflow-x: hidden;
	overflow-y: hidden;
}
.bg1 {
	position: absolute;
	height: 100vh;
	z-index: 1;
	width: 45%;
	transform: scaleX(-1);
	right: 0px;
	background-color: transparent;
	top: 0px;
}

.container1 {
	background-color: transparent;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr;
	padding-top: 0%;
}
.container1 .text {
	align-self: center;
	text-align: center;
	margin-top: -10%;
}
.container1 .text h1 {
	font-size: 45px;
	margin: 2% 17%;
}

.container1 .text p {
	font-size: 25px;
	margin: auto 15%;
}
span {
	color: rgb(0, 102, 255);
}
.container1 .image {
	background-color: transparent;
	z-index: 2;
	margin-top: 3%;
}
.container1 .me {
	width: 65%;
	background-color: transparent;
	margin-left: 5%;
}

@media (max-width: 670px) {
	.bg1 {
		position: absolute;
		z-index: 1;
		width: 100%;
		transform: scaleX(-1);
		right: 0px;
		background-color: transparent;
		top: 0px;
		opacity: .6;
	}
	.container1 {
		height: 90vh;
		background-color: transparent;
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr;
	}
	.container1 .text {
		align-self: center;
		text-align: center;
		margin-top: -15%;
		grid-row: 2;
		z-index: 2;
		background-color: transparent;
	}
	.container1 .text h1 {
		font-size: 33px;
		padding-bottom: 2%;
		margin: 0%;
		background-color: transparent;
	}

	.container1 .text p {
		font-size: 25px;
		background-color: transparent;
		margin: auto;
	}
	span {
		color: rgb(0, 102, 255);
		background-color: transparent;
	}
	.container1 .image {
		background-color: transparent;
		z-index: 2;
		text-align: center;
		margin-top: 10%;
	}
	.container1 .me {
		width: 85%;
		background-color: transparent;
	}
}
