* {
	margin: 0;
	padding: 0;
	background-color: #f4f4f4;
}

.navbar {
	z-index: 2;
	background-color: transparent;
}
.navbar ul {
	display: flex;
	justify-content: space-between;
	width: 30%;
	text-align: center;
	margin-left: 34.5%;
	margin-top: 1%;
	font-size: 18px;
}
.navbar ul li {
	list-style-type: none;
}
.navbar ul li .link {
	text-decoration: none;
	color: black;
}
.navbar ul li .link:hover {
	opacity: .8;
	transition: .5s;
	color: blue;
}
.hr3 {
	width: 30%;
	margin-left: 34.5%;
	margin-top: -5px;
}
.hr4 {
	padding-bottom: 3%;
	width: 30%;
	margin-left: 34.5%;
	margin-top: -12px;
}

.burger {
	display: none;
}
.burgerx {
	display: none;
}
.color {
	display: none;
}
@media (max-width: 670px) {
	.navbar {
		z-index: 33;
		position: fixed;
	}
	nav {
		position: fixed;
	}
	.color {
		position: fixed;
		width: 45%;
		height: 100%;
		left: 0;
		background: rgb(252, 252, 252);
		border-top-right-radius: 10px;
		border-bottom-right-radius: 10px;
		z-index: 9;
		opacity: .95;
	}
	.navbar ul {
		display: none;
		position: fixed;
		top: 60px;
		text-align: left;
		left: -100px;
		background-color: transparent;
	}
	.navbar ul li {
		margin: 30% auto;
		font-size: 25px;
		background-color: transparent;
	}
	.burger div {
		background-color: rgba(0, 0, 0, 0.541);
		width: 35px;
		height: 3px;
		margin: 20%;
	}
	.burgerx {
		position: fixed;
		float: right;
		margin-top: 30px;
		margin-left: 0%;
		background-color: transparent;
	}
	.burgerx div {
		background-color: rgba(0, 0, 0, 0.541);
		width: 35px;
		height: 3px;
		margin: 20%;
	}

	.burger {
		position: fixed;
		display: block;
		margin-top: 30px;
		margin-left: 0%;
		background-color: transparent;
	}
	ul li a {
		color: black;
	}
}
