.grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr;
	margin-top: -3%;
	background-color: transparent;
}

.image img {
	width: 100%;
}
.aboutmetext {
	margin: auto 5%;
}
.container3 h1 {
	text-align: center;
}
.aboutmeimages {
	text-align: center;
}
.aboutmeimages ul {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	margin-top: 8%;
}
.aboutmeimages img {
	width: 64px;
}
.aboutmeimages .react {
	width: 90px;
	margin: -5px;
}
.aboutmeimages p {
	color: blue;
	font-size: 20px;
	padding-top: 10%;
}
@media (max-width: 670px) {
	.grid {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr;
		margin-top: -3%;
		background-color: transparent;
	}

	.image img {
		width: 100%;
	}
	.aboutmetext {
		margin: 10% 5%;
	}
	.container3 h1 {
		text-align: center;
		margin-top: 50px;
		font-size: 35px;
	}
	.aboutmeimages {
		text-align: center;
		padding-top: 5%;
	}
	.aboutmeimages ul {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		row-gap: 20px;
		margin-top: 10%;
	}
	.aboutmeimages img {
		width: 64px;
	}
	.aboutmeimages .react {
		margin-left: 15px;
		grid-column: 2;
	}
	.aboutmeimages .react img {
		width: 90px;
	}
	.aboutmeimages p {
		color: blue;
		font-size: 20px;
		padding-top: 10%;
	}
}
